
* {
  box-sizing: border-box;
}

svg {
  width: 100%;
  height: 100%;
  display: block;
  fill: currentColor;
}

body {
  margin: 0;
  font-size: 16px;
  font-family: Roboto, Arial, sans-serif;
  position: relative;
}

html,
body {
  height: 100%;
}

button,
textarea {
  margin: 0;
  padding: 0;
  border: none;
  border-radius: 0;
  background-color: transparent;
  color: inherit;
  box-shadow: none;
  font: inherit;
  letter-spacing: inherit;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

.extension {
  min-width: 500px;
}

.export-icons {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 50px 15px 15px;

  &__workspace {
    width: 100%;
  }

  &__result {
    margin-top: 50px;
  }

  &__version {
    position: absolute;
    right: 15px;
    top: 15px;
    color: #b5b5b5;
  }
}

.export-icons-workspace {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__textarea {
    resize: none;
    width: 100%;
    padding: 15px;
    border: 1px solid #00375e;
    border-radius: 4px;
    transition: border-color 0.2s ease-in-out;

    &:focus {
      outline: none;
      border-color: lighten(#00375e, 15%);
    }
  }

  &__buttons {
    margin-top: 25px;
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
}

.button {
  max-width: 100%;
  min-height: 40px;

  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 15px 30px;
  text-align: center;
  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out, border 0.2s ease-in-out;

  &--is-rounded {
    border-radius: 40px;
  }

  &--icon-only {
    padding: 10px;

    .button__icon {
      width: 50px;
      height: 50px;

      .icon__svg {
        fill: #fff;
      }
    }
  }

  &__container {
    flex: auto 0 1;
    min-width: 0;
    white-space: nowrap;
  }

  &__label {
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #ffffff;
  }

  &--theme-primary {
    background-color: #276aa5;

    &:hover {
      background-color: #1f5584;
    }
  }

  &--theme-secondary {
    background-color: #25a248;

    &:hover {
      background-color: #229643;
    }
  }
}

.export-icons-table {
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  list-style: none;
  grid-gap: 20px;

  &__cell {
    display: flex;
    align-items: center;
    padding: 15px;
  }
}

.icon-single {
  &__icon {
    display: block;
    background: url('assets/images/icon_bg.jpg') no-repeat center/cover;
    width: 150px;
    height: 150px;
    padding: 15px;

    .icon__svg {
      fill: #fff;
    }
  }

  &__actions {
    display: flex;
    align-items: center;
  }

  &__action {
    flex: 1 1 50%;
    max-width: 50%;
  }
}

.icon {
  display: flex;
  justify-content: center;
  align-items: center;
}
